<template>
  <section class="col-lg-10 col-lg-push-2" >
    {{init}}
    <div v-for='value in posts'>
      <header class="col-lg-10 center-block" >
        <figure class="figure">
          <img v-bind:src="value['imgBanner']" class="img-fluid img-responsive" alt="...">
          <figcaption class="figure-caption text-muted"><b>Foto: {{ value['titulo'] }}</b></figcaption>
          <div class="text-muted">
            <b>Dia da postagem: {{ value['dataPostagem'] }} <br/>
              Hora da postagem: {{ value['hora'] }} <br/>
              Autor: {{ value['autor'] }}<br/></b>
          </div>
        </figure>
      </header>
      <article>
        <dl>
          <dt class="col-lg-10">
            <h2 class="text-center">
              <b>{{ maiuscula(value['titulo'])}}</b>
              <hr/>
            </h2>
          </dt>
          <dd class="text-justify col-lg-10" >
            <div v-html="value['publicacao']" class="col-lg-12"></div>
            <V-Video>
              <slot slot='titulo'>
                <a :href="value['url']" target="_blank" class="btn-link">
                  <h3 class="text-center">{{value['titulo'].toUpperCase()}}</h3>
                </a>
              </slot>
              <slot slot='frame'>
              <iframe class="center-block" width="560" height="315" :src="value['link']"
              frameborder="0" allowfullscreen></iframe>
            </slot>
            </v-Video>
        </dd>
        </dl>
      </article>
      <!-- 0800 7212054
      www.paguefacil.com.br
      parcela 177 / 200 / -->

      <div class="col-lg-12 " id='voltar'>
        <router-link :to="{ name: 'home.blog'}" class="col-lg-2 col-lg-push-4 btn btn-lg btn-primary">
          Voltar...</router-link>
      </div>
    </div>
  </section>
</template>
<script>
import { postsRef } from '@/firebase'
import VVideo from '@/components/layout/widgets/Video'
export default {
  props: [ 'to', 'titulo', 'id' ],
  name: 'Post',
  components: { VVideo },
  data: () => {
    return {
      rotulo: 'Heroku',
      dataPostagem: '233o3',
      horaPostagem: '2333232',
      autor: 'casdadada',
      postagem: 'adadadada',
      link: '',
      url: '',
      imgTeste: ''
    }
  },
  firebase: {
    posts: postsRef
  },
  methods: {
    dbPostagem () {
      postsRef.orderByChild('heroku').equalTo('heroku').on('value', function (snapshot) {
        console.log(snapshot.val())
      })
    },
    maiuscula (valor) {
      return valor.toUpperCase()
    }
  },
  computed: {
    init: function () {
      var varTitle = document.getElementById('title')
      varTitle.innerHTML = this.maiuscula(this.$props.titulo)
    }
  }
}
</script>
<style scoped>
  img {
    height: 250px;
    width: 1080px;
  }
  .sublinhado {
      text-decoration:underline;
  }
  #voltar{
    margin-top: 2.5%
  }
</style>



