<template>
  <div>
    {{init}}
    <hr/><h1 class="text-center">Destaques da semana</h1><hr/>
    <div v-for="(itens , index) in Item" :key="itens.index">
      <div class="card col-lg-4">
        <img src="../../../assets/php.jpg" class="card img-fluid img-rounded img-responsive" v-bind:alt='titulo'>
        <article>
          <p><em>PHP sem framewok</em></p>
          <dl><dt>Hospedando site no heroku</dt>
            <dd><p>Veja como hospedar seu site na heroku.com passo a passo</p>
              <router-link :to="{ name: 'home.post', params: { titulo: 'hospedando site no heroku' }}" type='submit' class="btn btn-success borderButton" >Veja mais</router-link>
            </dd></dl>
          </article>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Blog',
  data: () => {
    return {
      title: 'Destaque da Semana',
      titulo: 'imagem',
      Item: [
        { key: 1 }, { key: 2 }, { key: 3 }, { key: 4 }
      ]
    }
  },
  computed: {
    init: function () {
      var varTitle = document.getElementById('title')
      if (varTitle.innerHTML === '') {
        varTitle.innerHTML = 'Destaques da semana'
      } else {
        varTitle.innerHTML = 'Destaques da semana'
      }
    }
  }
}
</script>
<style>
  .borderButton{
    border: 0;
    border-color: none
  }
</style>



